@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,500;1,400;1,700&display=swap');



*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
  }

.main_div {
  width: 100%;
  height: 100vh;
  background: #badc58;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.center_div {
  width: 25%;
  height: 60vh;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
h1 {
    color: white;
    background: transparent;
    font-size: 50px;
    background-color: #30336b;
    padding: 6pxk 0 2px 0;
    margin-bottom: 10px;
    width: 100%;
    box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
  }  




  input{
  text-align:center;
  height: 30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid #8566aa;
  outline: none;
}

button{
    min-height: 40px;
    /* width: 40px; */
    border-radius: 5%;
    border-color: transparent ;
    background-color: #30336b;
    color: #fff;
    font-size: 20px;
    border: none;
    outline: none;
    padding: 0 15px;
    margin-left: 30px;
    box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
}

button:hover{
  background-color: #22a6b3;
}

.btn_div{
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.btn_green:hover{
  color: #2ed573;
}

.btn_red:hover{
  color: #ff4757;
}




